.NavbarItems{
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 1.2rem;
    padding: 0 30px;
    background-color: #5fbe87;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.25);
    width: 99%;
    height: 80px;
    border-radius: 13px;
    z-index: 1000;
    position: fixed;
    top: 20px;
    left: 50%;
    transform: translate(-50%);
}
.NavbarItems img{
    /* background-image: url("../../images/logo.jpeg"); */
    width: 52px;
    height: 70px;
    /* margin-top: 40px; */
    background-size: contain;
    background-repeat: no-repeat;
}
.navbar-brand{
    color: white;
    justify-self: start;
    cursor: pointer;
    /* margin-left: 0.2rem; */
    /* font-size: 1rem; */
}
.nav-menu{
    display: grid;
    grid-template-columns: repeat(7,auto);
    grid-gap: 15px;
    list-style: none;
    color: black;
    align-items: center;
    text-align: center;
    justify-content: end;
    /* position: absolute; */
}
.nav-link{
    text-decoration: none;
    color: black;
    font-size: 15px;
    
}
.dropdown_menu{
    display: none;
}
.dropdown:hover .dropdown-menu{
    display: block;
    position: absolute;
    left: 0;
    top: 100%;
    background-color: white;
}
.menuul{
    display: block;
    margin: 10px;   
}
.menuul .menuli{
    display: flex;
    flex-direction: column;
    text-decoration: none;
    color: black;
    width: 150px;
    padding: 10px;
}
.nav-links{
    text-decoration: none;
    color: black;
    padding: 0.7rem 1rem;
    padding-left: 0px;
    font-size: 15px;
    white-space: nowrap;
}
.nav-links :hover{
    background-color: rgb(17, 17, 17);
    border-radius: 4px;
    transition: all 0.2s ease-in-out;
    height: 33px;
    color: aliceblue;
    width: 90px;
}
.menu-icons{
    display: none;
}
.menu-icons i{
    color: aliceblue;
}
@media screen and (max-width:991px ){
    .navbar-brand{
        font-size: 12px;
    }
}
@media screen and (max-width:333px ){
    .navbar-brand{
        display: none;
    }
}


@media screen and (max-width: 850px){
    .NavbarItems{
        z-index: 99;
    }
    .nav-menu{
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        background: rgba(255, 255, 255, .25);
        box-shadow: 0 8px  32px 0 rgba(31, 38, 135, .37);
        backdrop-filter: blur(4px);
        border-radius: 13px;
        width: 100%;
        height: auto;
        position: absolute;
        top: 0;
        left: -110%;
        align-items: stretch;
        padding: 80px 0 30px 0;
        margin: 0;
        z-index: -1;
        transition: all 0.3s ease-in-out;
    }
    .nav-menu.active{
        left: 0%; 
    }
    .nav-links{
        display: block;
        widows: 100%;
        padding: 2rem 0;
        color: black;
    }
    .nav-links:hover{
        background: rgba(255, 255, 255, 0.25);
        backdrop-filter: blur(20px);
    }
    .menu-icons{
        display: block;
    }
}









