.main_footer{
    height: 60vh;
    background-color: black;
}
.footerlinks{
    display: flex;
    flex-direction: row;
    gap: 40px;
    padding: 50px 10px 10px 10px;
    justify-content: center;
    align-items: center;
}
.foter_links{
    text-decoration: none;
    color: white;
    font-size: 17px;
    display: flex;
    flex-direction: row;
}
.form{
    display: flex;
    flex-wrap: wrap;
    padding-top: 40px;
    justify-content: center;
}
.form label{
    color: white;
    font-size: 14px;
    padding-right: 20px;
}
.form  input{
    width: 40vw;
    /* border: 1px solid red; */
}
.form button{
    margin-left: 10px;
}
.icon{
    display: flex;
    font-size: 20px;
    padding-top: 20px;
    gap: 10px;
    justify-content: center;
}
hr{
    color: white;
}
.copyright{
    padding-top: 50px;
    display: flex;
    justify-content: center;
}
.copyright p{
    font-size: 16px;
    color: white;
}
@media screen and (max-width:390px ){
    .footerlinks .foter_links{
        font-size: 10px !important;
    }
    .form{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 2px;
        gap: 5px;
    }
    .form input{
        border: none;
        border-radius: 2px;
        height: 25px;
    }
    .form button{
        padding-top: 5px;
    }
}
@media screen and (max-width:490px ){
    .footerlinks .foter_links{
        font-size: 15px;
    }
    .main_footer{
        height: 40vh;
    }
    .copyright p{
        font-size: 10px;
    }
} 