/*=======================================*/
/********homepage========================*/
.home{
  margin-top: 130px;
  background-image: url("./images/c3.jpg");
  height: 400px;
    width: 100%;
    background-size: cover  ;
    background-repeat: no-repeat;
    background-position: top 56% right 50%;
    padding: 10px 3rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-family: 'Varela Round', sans-serif;
    color: white;
    transition: background 0.3s, border 0.3s, border-radius 0.3s, box-shadow 0.3s;
  }
.home .homemainheading{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-weight: 900;
}
.home .homemainheading:hover{
  transition: all .5s ease;
  color: #00A108;
  font-weight: 700;
}
.home .reservation{
  width: 100%;
  padding: 22px 54px 22px 45px;
  font-family: "Lato", Sans-serif;
  font-size: 15px;
  font-weight: 700;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #00A108;
  border-style: solid;
  border-width: 1px 1px 1px 1px;
  border-color: #4481fe;
  border-radius: 49px 49px 49px 49px;
  box-shadow: 19px 19px 40px 0px rgba(0,0,0,0.1);
}
.home .reservation:hover{
  background-color: white;
  color: #00A108;
  transition: all .5s ease;
}
.homesecondary{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 120px;
}
.homesecondarytitle{
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
}
.homesecondarytitle p{
  color: #FFFFFF;
  position: absolute;
    font-family: "Archivo", Sans-serif;
    font-size: 90px;
    font-weight: 700;
    line-height: 1em;
    letter-spacing: -0.5px;
    text-shadow: 0px 0px 1px #585367;
    mix-blend-mode: multiply;
    z-index: 1;
}
.homesecondarytitle h2{
  color: #585367;
  margin: 0px 0px 10px 0px;
  font-family: "Archivo", Sans-serif;
  font-size: 45px;
  font-weight: 600;
  letter-spacing: -1px;
  z-index: 2;
}
.homesecondarytitle span{
  font-family: "Playfair Display", Sans-serif;
  font-weight: 700;
  color: #139000;
  font-style: italic;
}
.homesecondarytitle span{
  transition: all .3s ease;
    display: inline-block;
}
.flexcards{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  overflow: hidden;
}
.containerflex{
  display: flex;
  width: 90vw;
}
.panel{
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 70vh;
  flex: 1;
  position: relative;
  transition: flex 0.5s ease-in;
  color: white;
  cursor: pointer;
  margin-left: 2px;
}
.panel h4{
  position: absolute;
  bottom: 90px;
  display: flex;
  align-items: center;
  justify-content: center;
  left: 20px;
  opacity: 0;
}
.panel a{
  padding: 19px 34px 18px 34px;
  font-family: "Archivo", Sans-serif;
  font-size: 15px;
  font-weight: 500;
  color: #09A300;
  background-color: #FFFFFF;
  border-radius: 6px 6px 6px 6px;  
  position: absolute;
  bottom: 18px;
  margin-left: 80px;
  opacity: 0;
  text-decoration: none;
}
.panel.active{
  flex: 3 ;
}
.panel:hover{
  flex: 3;
  opacity: 3;
}
.panel:hover h4{
  opacity: 3;
}
.panel:hover a{
  opacity: 3;
}
.panel.active h4{
  opacity: 3;
}
.panel.active a{
  opacity: 3;
}
@media(max-width:480px){
  .containerflex{
    width: 100vw;
    height: 60vh;
