
.sidebar {
    display: flex;
    flex-direction: column;
    position: relative;
    padding-top: 4rem;
    transition: all 300ms ease;
  }
  /* logo */
  .bars{
    display: none;
  }
  
  .logo {
    display: flex;
    height: 5rem;
    font-weight: bold;
    font-size: 22px;
    gap: 1rem;
    align-items: center;
    justify-content: center;
    height: 4%;
  }
  .logo > span > span {
    color: var(--pink);
  }
  
  .logo > img {
    width: 3rem;
    height: 3rem;
  }
  
  
  /* adminmenu */
  .adminmenu {
    margin-top: 4rem;
    display: flex;
    flex-direction: column;
    gap: 2rem;
}
h4{
  padding-left: 30px;
}
.menuItem {
    color: black;
    background-color: rgb(219, 174, 219);
    text-decoration: none;
    display: flex;
    align-items: center;
    gap: 0.6rem;
    height: 2.5rem;
    margin-left: 7px;
    padding-left: 2rem;
    transition: all 300ms ease;
    border-radius: 0.7rem;
    font-size: 14px;
  }
  
  .menuItem:hover {
    cursor: pointer;
   }
  
  /* .adminmenu .menuItem:last-child {
    position: absolute;
    bottom: 2.3rem;
    width: 100%;
  } */
  
  .adminmenu .active {
    background-color: rgb(248, 153, 153);
    /* margin-left: 0; */
  }
  
  
  
  /* Tablets */
  @media screen and (max-width: 1200px) {
    .menuItem>span{
      display: none;
    }
    .logo{
      display: none;
    }
    .adminmenu .active {
      background-color: rgb(248, 153, 153);
      /* margin-left: 0; */
    }
  }
  
  @media screen and (max-width: 768px) {
    .sidebar{
      position: fixed;
      z-index: 9;
      background: #ffe0e0;
      width: 55%;
      padding-right: 1rem;
      height: 100%;
    }
    .menuItem>span{
      display: block;
    }
    .logo{
      display: flex;
    }
    .adminmenu .menuItem:last-child {
      position: relative;
      margin-top: 6rem;
    }
    .bars{
      display: flex;
      position: fixed;
      top: 2rem;
      left:60%;
      background:#ffe0e0;
      padding:10px;
      border-radius: 10px;
      z-index: 9;
    }
    .close{
      left: -60%;
    }
    .adminmenu .active {
      background-color: rgb(248, 153, 153);
      /* margin-left: 0; */
    }
  }